import React, { Component } from 'react'
import { Container } from 'react-bootstrap';

class GDPR extends Component {
    render() {
        return (
            <Container>
                <section class="about py-5" id="GDPR">
                <div className="container">
                    <div className="row">
                        <div className="col-4 mx-auto col-md-12 my-5">
                            <h3 className="text-capitalize"><center>Politica de confidenialitate i siguran privind datele cu caracter personal</center></h3>
                            <br></br>
                            <p className="my-4 text">
                                <br></br>
                                <center><strong>Ce este GDPR?</strong></center>
                                <br></br>
                                GDPR este acronimul pentru General Data Protection Regulation (GDPR) i se refer la Regulamentului Uniunii Europene Nr. 679/2016 privind protecia, confidenialitatea i accesul la datele cu caracter personal ale persoanelor fizice de pe teritoriul Uniunii Europene (UE) i Zonei Economice Europene (ZEE). Acest regulament se aplic ncepnd cu 25 mai 2018 de ctre toate statele membre ale UE.
                                Fr a reproduce regulamentul mpreun cu prevederile acestuia (l gsii accesnd acest link), vom sintetiza aici rspunsuri la punctele cheie care se aplic n cadrul organizaie noastre:
                                <br></br>
                                <br></br>
                                <center><strong>Ce date personale colecteaz Mano Libre Coffee Roasters i prin ce metode?</strong></center>
                                <br></br>
                                Mano Libre Coffee Roasters colecteaz exclusiv datele personale necesare ncheierii tranzaciei pentru care clientul i-a dat consimmntul. Acestea sunt informaiile minime cerute de legislaia romn n vigoare i sunt necesare facturrii de produse i servicii. Adiional, pentru tranzaciile care implic expedierea de produse, vor fi solicitate strict datele necesare derulrii acestui serviciu. Orice date suplimentare oferite, sunt opionale (nu sunt necesare pentru ncheierea unei tranzacii) i nu fac obiectul datelor cu caracter personal. Odat cu transmiterea unei comenzi, aceste date pot fi colectate electronic (pe site sau prin email), telefonic sau personal.
                                Mano Libre Coffee Roasters nu colectez sau proceseaz alte date cu caracter personal n scopuri de analize de marketing sau reclame.
                                <br></br>
                                <br></br>
                                <center><strong>Cine are acces la aceste date i cum sunt prelucrate? Sunt acestea partajate cu pri tere?</strong></center>
                                <br></br>
                                Datele cu caracter personal sunt prelucrate de ctre NovoRoastery SRL (Mano Libre Coffee Roasters), avnd datele de identificare specificate la seciunea Contact al site-ului Mano Libre Coffee Roasters. La datele colectate i stocate n sistemul software are acces exclusiv personalul Mano Libre Coffee Roasters implicat n operaiunile aferente tranzaciilor comerciale, respectiv operaiunile de vnzare, facturare, ambalare i expediere. Datele minime necesare expedierii de produse (dac s-a optat pentru acest serviciu) vor fi partajate cu serviciul de curierat folosit n tranzacia respectiv. Adiional, n concordan cu legislaia n vigoare, Mano Libre Coffee Roasters furnizeaz datele necesare raportrilor financiare ctre administraiile naionale, n regim periodic sau pe baz de solicitare legal.
                                Orice activiti secundare desfurate de Mano Libre Coffee Roasters (ex. campanii de promovare, comunicri, etc.) se bazeaz pe consimmntul dat explicit pentru aceste scopuri.
                                Mano Libre Coffee Roasters nu partajeaz datele personale cu nicio alt ter parte i pentru niciun alt scop dect cele enunate mai sus.
                                <br></br>
                                <br></br>
                                <center><strong>Unde sunt pstrate datele colectate? Sunt acestea n siguran?</strong></center>
                                <br></br>
                                Pe suport fizic, datele sunt pstrate n arhiva financiar proprie, cu acces exclusiv al personalului financiar. Electronic, datele sunt pstrate pe sisteme securizate pe multiple nivele iar copiile de siguran sunt criptate n mod automat i pstrate sub aceast form. Colectarea datelor prin intermediul site-ul Mano Libre Coffee Roasters se efectueaz criptat, pe baza unui certificat valid, rennoit periodic.
                                <br></br>
                                <br></br>
                                <center><strong>Cum pot s-mi accesez datele cu caracter personal, s le rectific sau s le terg?</strong></center>
                                <br></br>
                                Datele furnizate prin intermediul site-ului Mano Libre Coffee Roasters pot fi accesate prin autentificare n sistem folosind contul creat n prealabil sau odat cu furnizarea iniial a acestor date sau printr-o solicitare scris ctre YUME. n acest scop, folosii datele de contact precizate la seciunea Contact al site-ului Mano Libre Coffee Roasters. Mano Libre Coffee Roasters v va furniza la cerere toate datele proprii cu caracter personal deinute n sistemul informatic, ntr-un format portabil i uor accesibil, mpreun cu orice alte informaii legate de acestea.</p>
                        </div>
                    </div>
                </div>  
                </section>          
        </Container>

        )
    }
}

export default GDPR;